.catalog{background:#fff;border-radius:5px;padding:10px 52px;padding-bottom:40px;position:relative;margin-top:-27px;z-index:2}.catalog .category_title{color:#7c7284;font-weight:400;margin-bottom:20px;margin-top:20px;font-size:18px;text-transform:capitalize;cursor:pointer;transition:.3s all}.catalog .category_title:hover{color:#f86384}.catalog .items{margin-left:50px;margin-top:50px;background:#e9e8f8;transition:.4s all ease-in-out;color:#21273d;cursor:auto;overflow:hidden}.catalog .items:hover{transform:scale(1.1);box-shadow:7px 7px 8px #444}.catalog .items_container{display:flex;flex-wrap:wrap;justify-content:space-between;margin-left:-50px}.catalog .items_img{height:286px;cursor:pointer}.catalog .active{color:#f86384}.items_body{position:relative}.items_body div{margin:10px 0 15px 0}.btn{width:100%}.btn:first-child{background:#fff}.btn:last-child{margin-top:20px}.btn a{color:#fff}.like{position:absolute;right:10px;top:0;height:fit-content}.like i{cursor:pointer;padding:10px 12px 8px;background:#fff;border-radius:50%;display:inline-block;color:#aaa;transition:.2s}.like i:before{font-family:fontawesome;content:"";font-style:normal}.like span{position:absolute;left:0;right:0;visibility:hidden;transition:.6s;z-index:-2;font-size:2px;color:transparent;font-weight:400}.press i{animation:size .4s;color:#fa6585}.press span{bottom:120px;font-size:14px;visibility:visible;animation:fade 1s}@keyframes fade{0%{color:red}50%{color:red}100%{color:red}}@keyframes size{0%{padding:10px 12px 8px}50%{padding:14px 16px 12px;margin-top:-4px}100%{padding:10px 12px 8px}}