.basket{background:#fff;border-radius:5px;padding:10px 52px;padding-bottom:40px;position:relative;margin-top:-27px;z-index:2;min-height:calc(100vh - 246px)}.basket h1{color:#152b3c;font-weight:700;margin-top:50px;font-size:30px}.item{border:1px solid rgba(68,68,68,.52);height:170px;background:#e9e8f8;border-radius:10px;padding:20px;margin-top:40px;display:flex;align-items:center;justify-content:space-between}.item img{height:inherit;padding:10px}.item p{font-size:18px;font-weight:600}.item div{font-size:25px;transition:.8s;cursor:pointer;color:red}.item div:hover{transform:rotate(360deg)}.accept{display:flex;justify-content:flex-end;padding:20px 0}.accept_box p{font-size:18px;font-weight:600}.accept_box button{float:right}.like{position:absolute;right:20px;bottom:15px;height:fit-content}.like i{cursor:pointer;padding:10px 12px 8px;background:#fff;border-radius:50%;display:inline-block;color:#aaa;transition:.2s}.like i:before{font-family:fontawesome;content:"";font-style:normal}.like span{position:absolute;left:0;right:0;visibility:hidden;transition:.6s;z-index:-2;font-size:2px;color:transparent;font-weight:400}.press i{animation:size .4s;color:#fa6585}.press span{bottom:120px;font-size:14px;visibility:visible;animation:fade 1s}@keyframes fade{0%{color:red}50%{color:red}100%{color:red}}@keyframes size{0%{padding:10px 12px 8px}50%{padding:14px 16px 12px;margin-top:-4px}100%{padding:10px 12px 8px}}.items{margin-left:50px;margin-top:50px;background:#e9e8f8;transition:.4s all ease-in-out;color:#21273d;cursor:auto;overflow:hidden}.items:hover{transform:scale(1.1);box-shadow:7px 7px 8px #444}.items_container{display:flex;flex-wrap:wrap;margin-left:-50px}.items_img{height:300px}